    <view v-if="statusDTO == 6" class="content_message"><image mode="scaleToFill" src="../../static/icon/messageError3x.png" /><text class="message_error">您的申请已被驳回，请点击确认按钮关闭</text></view>
    <view v-if="statusDTO == 2" class="content_message_success"><image mode="scaleToFill" src="../../static/icon/messageSuccess3x.png" /><text class="message_success">您的申请已受理，正在协调交警队员！</text></view>
    <view v-if="statusDTO == 3" class="content_message_success"><image mode="scaleToFill" src="../../static/icon/messageEnd3x.png" /><text class="message_success">您的申请已受理，已协调交警队员！</text></view>
    <view v-if="statusDTO == 3" class="content_header">
