      <view class="content_header_info">
        <view class="content_header_info_plate">123</view>
        <view class="content_header_info_name">警员姓名：123</view>
        <view class="content_header_info_road">途径：123</view>
      </view>
      <view class="content_header_phone">
        <view class="content_header_phone_icon"><image @click="phone('18072729393')" mode="scaleToFill" src="../../static/icon/policePhone3x.png" /></view>
        <view>电话</view>
      </view>
    </view>
    <view class="content_info">
      <view class="content_info_label">姓名：<text>{{ params.createUserName }}</text></view>
      <view class="content_info_label">联系电话：<text>{{ params.phone }}</text></view>
      <view class="content_info_label">起点：<image mode="scaleToFill" src="../../static/icon/startAdress3x.png" /><text>{{ params.startAddress }}</text></view>
      <view class="content_info_label">终点：<image mode="scaleToFill" src="../../static/icon/endAdress3x.png" /><text>{{ params.endAddress }}</text></view>
      <view class="content_info_label">约定出发时间：<text>{{ params.appointDate }}</text></view>
      <view class="content_info_label_textarea">
        <view class="content_info_label_textarea_name">情况描述：</view>
        <view class="content_info_label_textarea_content">{{ params.description }}</view>
      </view>
      <view class="content_info_label_photo">
        <view class="content_info_label_photo_name">照片：</view> 
        <view class="content_info_label_photo_content">
				  <image class="image" v-for="item in params.fileUrls" :key="item" mode="scaleToFill" @click="previewImg()" :src='item' />
        </view>
      </view>
    </view>
    <view class="steps-list">
      <view class="steps-list_header">
        <view class="steps-list_name">处理进度</view>
      </view>
      <view class="content-step">
        <stepsList lineNum=0 color='#fff' backgroundColor='#218AFF' :infoList='list'></stepsList>
      </view>
    </view>
    <view v-if="statusDTO == 1" class="content_cancel">取消申请</view>
    <view v-else class="submit" @click="goEmergencyCoures">
      <button>确 定</button>
    </view>
  </view>
</template>
<script>
import stepsList from '@/components/steps-list/steps-list.vue'
	export default {
    components: {
      stepsList
    },
		data() {
			return {
				title: 'picker',
        params:{
          createUserName: null,
          phone: null,
          startAddress: null,
          endAddress: null,
          appointDate: null,
          description: null,
          fileUrls: []
        },
        statusDTO: null,
        list: [
          {
            title: '用户申请紧急定制路线',
            date: '2022-11-05 10:22',
          },{
            title: '客服已驳回',
            date: '2022-11-05 10:22',
            rejectedRemark: '史蒂夫个路i可以快乐就好了快结束的法律框架啊合适的饭离开家阿斯顿了空间发挥立刻结婚啊谁来对抗肌肤啊手里的抠脚大汉法律开始觉得很烦阿莱克斯接电话发来开始觉得很烦'
             
          }
        ]
			}
		},
    onLoad(options){
      // 
      console.log('options',options)
      this.$http.planRouteDetail({'id':options.id}).then(res=>{
        console.log(res)
        this.params = res.data
        this.imgItems = res.data
        let dto = res.data.planRouteDetailDTOS
        let array = []
        dto.forEach(e => {
          console.log(e)
          if(e.status == 1){
            e.title = '用户申请紧急定制路线'
          }else if(e.status == 2){
            e.title = '已受理'
          }else if(e.status == 3){
            e.title = '协调'
          }else if(e.status == 4){
            e.title = '处理完成'
          }else if(e.status == 5){
            e.title = '已取消'
          }else if(e.status == 6){
            e.title = '已驳回'
          }else if(e.status == 9){
            e.title = '处理结束'
          }
          let plan = {
            title: e.title,
            date: e.appointDate,
            rejectedRemark: e.rejectedRemark,
            status: e.status
          }
          array.push(plan)
        })
        console.log('array =>',array)
        this.list = array
        this.statusDTO = array[array.length - 1].status
        console.log('statusDTO =>',this.statusDTO)
      })
    },
		methods: {
      goEmergencyCoures(){},
      previewImg() {
        wx.previewImage({
					urls: this.params.fileUrls, //需要预览的图片http链接列表，多张的时候，url直接写在后面就行了
					current: this.params.fileUrls[0], // 当前显示图片的http链接，默认是第一个
					success: function(res) {},
					fail: function(res) {},
					complete: function(res) {},
				})
		  },
      phone(e){
        uni.makePhoneCall({
          phoneNumber: e, 
          // 成功回调
          success: (res) => {},
          fail: (res) => {}

          });
      },
    }
  }
</script>

